.todo-item {
  width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  padding: 20px 0 20px 50px;
  background-color: rgb(249, 249, 249);
  border-bottom: 1px solid rgb(172, 172, 172);

  @media (max-width: 576px) {
    padding: 10px 0 10px 40px;
  }

  &__icon {
    position: absolute;
    left: 10px;

    @media (max-width: 576px) {
      width: 20px;
      height: 20px;
    }
  }

  &__text.completed {
    text-decoration: line-through;
    color: rgb(113, 113, 113);
  }
}