.todo-footer {
  width: 100%;
  padding: 10px 15px;
  display: flex;
  justify-content: space-between;
  font-size: 1.5rem;
  background-color: rgb(249, 249, 249);
  color: rgb(113, 113, 113);

  @media (max-width: 576px) {
    font-size: 1.2rem;
  }

  // Эффект стопки бумаг
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15), 0 10px 0 -5px #eee,
  0 10px 1px -4px rgba(0, 0, 0, 0.15), 0 20px 0 -10px #eee,
  0 20px 1px -9px rgba(0, 0, 0, 0.15);

  button {
    margin: 0 5px;
    padding: 6px;
    // Чтобы после добавления рамки в фокусе не съезжал текст пришлось добавить рамку такого же цвета
    border: 1px solid rgb(249, 249, 249);
    background-color: rgb(249, 249, 249);
    color: rgb(113, 113, 113);

    &:hover {
      color: red;
    }

    &:focus {
      color: red;
    }

    @media (max-width: 576px) {
      margin: 0 2px;
      padding: 2px;
    }
  }
}